.item-block {
	position: relative;
  margin: 20% 10% 0 10%;
  padding-top: 5%;
  color: get($colors, $white);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: get($colors, white);
  }

  .item-title {
    position: relative;
    padding-left: 21px;

		@include font-size(30px);

    &::before {
      content: "";
      position: absolute;
			top: 50%;
			transform: translateY(-50%);
      left: 0;
      border-radius: 50%;
      height: 9px;
      width: 9px;
      background-color: get($colors, orange);
    }
  }
}

.item--pink,
.item--lightgreen,
.item--dirtygreen,
.item--lightblue,
.item--blue,
.item--darkblue,
.item--purple,
.item--dingypurple {
	@include transition(background-size, .5s);

	background-image: url('https://picsum.photos/seed/picsum/200/300');
  background-size: 100% 0%;
	background-repeat: no-repeat;
  
	&:hover {
    background-size: 100% 100%;
  }

	@include media-breakpoint-down(md) {
		background-size: 100% 100%;
	}
}

.item--pink {
  background-color: get($colors, pink);
}

.item--lightgreen {
  background-color: get($colors, lightgreen);
}

.item--dirtygreen {
  background-color: get($colors, dirtygreen);
}

.item--lightblue {
  background-color: get($colors, secondary);
}

.item--blue {
  background-color: get($colors, blue);
}

.item--darkblue {
  background-color: get($colors, primary);
}

.item--purple {
  background-color: get($colors, purple);
}

.item--dingypurple {
  background-color: get($colors, dingypurple);
}