@import '../utilites/variables';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.logo {

  @include font-size(24px);
  @include transition(#{$transition-duration}s);

  color: get($colors, secondary);
  opacity: 1;

  @include media-breakpoint-down(lg) {
    display: none;
  }
}

.header {
  @include transition(#{$transition-duration}s);

  position: fixed;
  width: 100%;
  z-index: get($index, md);

  &.header--hide {

    .header__nav-link {
      padding: rem(2px) 0;
    }
  }

  &--hide {

    .header__search {

      @include media-breakpoint-up(lg) {
        opacity: 0;
        transform: scale(0);
      }
    }

    .logo,
    .button {
      transform: scale(0);
      // padding-top: 0;
      opacity: 0;
      cursor: auto;
    }
  }

  &--paint {
    background-color: get($colors, info);
  }

  &__nav {

    &-list {
      color: get($colors, primary);
    }

    &-link {
      @include font-size(14px);
      @include transition();

      position: relative;
      padding: rem(12px) 0;
      cursor: pointer;

      &--active {
        $underline-width: rem(50px);
        $underline-height: rem(2px);

        &::after {
          @include transition(width);

          content: "";
          position: absolute;
          bottom: 0;
          left: calc(50% - (#{$underline-width} / 2));
          width: $underline-width;
          height: $underline-height;
          background-color: get($colors, primary);
        }
      }
    }

    .header__nav-link+.header__nav-link {
      margin-left: rem(30px);
    }

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  .button {
    @include transition();

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  &__menu-burger {
    $menu-burger-width: rem(35px);
    $menu-burger-height: rem(22px);

    display: block;
    position: relative;
    width: $menu-burger-width;
    height: $menu-burger-height;
    z-index: get($index, xs);

    &.header__menu-burger--active {

      span {
        opacity: 0;
      }

      &::after,
      &::before {
        top: 50%;
      }

      &::after {
        transform: rotate(-45deg);
      }

      &::before {
        transform: rotate(45deg);
      }
    }

    span {
      display: block;
      top: 50%;
      transform: translateY(-50%);
    }

    span,
    &::after,
    &::before {
      @include transition(#{$transition-duration}s);

      content: '';
      position: absolute;
      width: 100%;
      height: rem(4px);
      left: 0;
      background-color: get($colors, black);
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    @include media-breakpoint-up(lg) {
      display: none;
    }
  }

  &__progressbar {
    height: rem(2px);
    width: 0;
    background-color: get($colors, secondary);
  }

  &__search {
    @include transition(#{$transition-duration}s);

    $button-size: rem(40px);
    $input-size: rem(200px);

    position: relative;
    width: $button-size;

    &-wrapper {
      @include transition(width, #{$transition-duration}s);

      position: absolute;
      right: $button-size;
      top: 0;
      width: 0;
      overflow: hidden;
    }

    &-button {
      width: $button-size;
      height: $button-size;
      padding: $button-size * .25;
      flex-shrink: 0;
    }

    &-submit {
			height: calc(#{$button-size} + 2px);
    }

    &-input {
      width: $input-size;
    }

    &.is-active {

      .header__search {

        &-wrapper {
          width: calc(#{$input-size} + #{$button-size});
        }
      }
    }
  }
}