@import '../../utilites/variables';
@import '../../utilites/mixins';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.intro {
  position: relative;
  max-height: 100vh;
  background-color: get($colors, light);
  overflow: hidden;

  &__content {
    padding-top: 4%;
    padding-bottom: 8%;
  }

  &__title {
    padding-top: 5%;
    padding-bottom: 8%;
    font-weight: 700;

		@include font-size(48px);
  }

  &__desc {
    line-height: 1.7;

		@include font-size(24px);
  }

  &__img {
    width: 60%;
    right: -$container-padding-x;

    &--front {
      z-index: get($index, xs);
    }

    &--back {
      z-index: get($index, default);
    }
  }
}