.offcanvas {
	background-color: get($colors, info);
	
	.menu-link {
		display: inline-block;
		font-size: 20px;
	}
	
	.menu-link + .menu-link {
		margin-top: 5%;
	}

	.btn-close {
		position: relative;

		&::before,
		&::after {
			content: '';
			position: absolute;
			background-color: get($colors, black);
			width: 100%;
			left: 0;
			height: 2px;
		}

		&::before {
			transform: rotate(45deg);
		}

		&::after {
			transform: rotate(-45deg);
		}
	}

	@include media-breakpoint-up(lg) {
		display: none;
	}
}

