@import '../../utilites/variables';
@import '../../utilites/mixins';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.contact {
	background-color: get($colors, primary);

	&__title {
		padding-top: 20%;
		padding-bottom: 10%;
	}

	&__img {
		right: -$container-padding-x;
	}

	&__content {
		color: get($colors, white);
	}

	&__desc {

		@include font-size(36px);
	}
}