.section-offset {
  background-repeat: no-repeat;

  @include media-breakpoint-down(md) {
    padding: 0 !important;
  }

  &--start {
    padding-left: 8%;
  }

  &--end {
    padding-right: 8%;
  }

  &__wrap {
    position: relative;


    &:before {
      @include pseudo;
      top: 0;
      left: -$container-padding-x;
      width: calc(100% + #{$container-padding-x * 2});
      height: 100%;
    }

    @include media-breakpoint-up(lg) {
      padding-top: 12%;
      padding-bottom: 7%;
    }

		@include media-breakpoint-down(lg) {
      padding-top: 10%;
      padding-bottom: 5%;
    }
  }

  &__content {
    position: relative;
    z-index: get($index, sm);
    width: 45%;

    @include media-breakpoint-down(md) {
      width: 100% !important;
      text-align: center;
      margin: 0 !important;
    }
  }

  &__label {
    text-transform: uppercase;
  }

  &__img {
    position: absolute;
    top: 0;
    z-index: get($index, default);
    height: 100%;
    width: 40%;
    object-fit: cover;
    overflow: hidden;

    &>img {
      height: 100%;
      object-fit: cover;
    }

    @include media-breakpoint-down(md) {
      display: none;
    }
  }

  &__title {

    &--big {

			@include font-size(64px);
    }

    &--middle {

			@include font-size(48px);
    }

    &--small {

			@include font-size(36px);
    }
  }

  &__desc {
    line-height: 1.75;
    padding-bottom: 15%;

    @include font-size(24px);
  }

  &__link {
    display: inline-block;
    padding: 20px;
    color: get($colors, warning);
    letter-spacing: .1rem;

    &:hover {
      background-color: get($colors, secondary);
      color: get($colors, white);
      transition: all .5s;
    }

    &--alt {
      color: get($colors, secondary);

      &:hover {
        background-color: get($colors, warning);
      }
    }
  }
}