@import '../../utilites/variables';
@import '../../utilites/mixins';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.info {

	@include media-breakpoint-between(sm, md) {
		margin: 0;
		padding: 0;
	}

	&__content {
		@include font-size(14px);
		
		width: 55%;

		@include media-breakpoint-up(md) {
			margin-left: 45%;
		}
	}

	&__block {

		@include media-breakpoint-up(md) {
			width: calc(100% / 3);
		}

		@include media-breakpoint-down(md) {
			width: calc(100% / 2);
		}
		
	}

	.info__block + .info__block {
		
		@include media-breakpoint-up(md) {
			margin-left: 5%;
		}

		@include media-breakpoint-down(md) {
			margin-top: 5%;
		}
	}

  &__title {
    font-weight: 700;

		@include font-size(14px);
    
		@include media-breakpoint-up(lg) {
			padding-bottom: rem(50px);
		}
  }

	&__desc {
		line-height: 1.75;
		opacity: 50%;

		@include font-size(14px);
	}

	&__img {
		left: -$container-padding-x;
	}

	&__link {

		&-list {
			margin-top: 15%;
		}
		
		&-tel {
			opacity: 50%;
		}

		&-mail {
			font-weight: 700;
			color: get($colors, blue);
		}
	}
}