@import '../../utilites/variables';
@import '../../utilites/mixins';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.plans {

	&__content-pc {
		
		@include media-breakpoint-down(lg) {
      display: none;
    }

		@include media-breakpoint-up(lg) {
      display: block;
    }
	}

	&__content-mobile {

		@include media-breakpoint-up(lg) {
      display: none;
    }
		
		@include media-breakpoint-down(lg) {
      display: block;
    }
	}

  .plan-item {
		padding: 10% 15%;

		.plan-block {
			
			@include media-breakpoint-down(lg) {
				text-align: center;
			}

			.block-desc {
				line-height: 1.75;
				color: get($colors, primary);
				opacity: 50%;

				@include media-breakpoint-up(lg) {
					max-width: rem(300px);
				}
			}
		}
  }
}