@import '../../utilites/variables';
@import '../../utilites/mixins';
@import 'node_modules/bootstrap/scss/vendor/rfs';

.fact {

	&__img {
		left: -$container-padding-x;
	}

	&__content {
		width: 50%;
		margin-left: 45%;
	}
	
	&__desc {
		padding-top: 30%;
		font-weight: 700;

		@include font-size(36px);

		span {
			color: get($colors, secondary);
		}
	}
}

